<template>
  <div id="app">
    <div v-if="isReady">
      <Particles v-bind:properties=getParticlesProps()></Particles>
    </div>
  </div>
</template>

<script>
import Particles from './components/Particles.vue';
import paper from 'paper';

export default {
  name: 'App',
  components: {
    Particles
  },
  data: function(){
    return {
      isReady: false
    }
  },
  methods: {
    getParticlesProps(){
      return {
        holderId: 'particles',
        width: this.canvasDimension,
        height: this.canvasDimension,
        isReady: this.isReady
      }
    }
  },
  mounted: function(){
    this.canvasDimension = 0.5 * Math.min(window.innerWidth, window.innerHeight);
    paper.install(window);
    console.log(window.paper);
    this.isReady = true;
  }
}
</script>

